.fog-floating,
.fog-floating1 {
  position: absolute;
  height: 50vh;
  width: 300vw;
  bottom: 0;
}
.fog-floating {
  background: url("https://www.aktivkredit.ru/ac/assets/img/clouds.png")
    repeat-x;
  animation: fog 160s linear infinite;
}
.fog-floating1 {
  background: url("https://www.aktivkredit.ru/ac/assets/img/clouds.png")
    repeat-x;
  animation: fog 240s linear infinite;
}
@keyframes fog {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-200vw, 0, 0);
  }
}
